* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-left: 40px;
  margin-top: 20px;
}
.country-list {
  list-style: none;
}

.country-item {
  display: flex;
  gap: 20px;
  align-items: center;
  font-family: Georgia, 'Times New Roman', Times, sans-serif;
  font-weight: 600;
  font-size: medium;
}

.label {
  display: flex;
  gap: 20px;
  align-items: center;
  margin-left: 40px;
}

.info {
  list-style: none;
  font-family: Georgia, 'Times New Roman', Times;
  font-size: 20px;
}

.info span {
  font-weight: bold;
}
